.note-container {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center; 
  margin-left: 20px;
}

#temp-icon{
  height: 30px;
  width: 33px;
  right: -34px;
  top :-4px;
  position: absolute;
}

.note-container_empty{
  width: 190px;
  height: 190px;
  top: 50%;
  right: 35%;
  position: absolute;
}

.note-container_notes {
  height: 90%;
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  overflow-y: scroll;
}
